.btn {
  &__feedback {
    display: none;
  }
  &__container {
    margin-top: @btn-margin;
  }

  &-text {
    .button-text;
  }

  &-text {
    background-color: @btn-color;
    color: @btn-color-inverted;
    border-radius: @btn-border-radius;
    padding: 16px 21px;
    width: 35% !important;
    min-width: 180px !important;
    font-weight: 900;
    margin-top: 10px;

    // .no-touch &:not(.is-disabled):not(.is-locked):hover {
    //   background-color: @btn-color-hover;
    //   color: @btn-color-inverted-hover;
    //   .transition(background-color @duration ease-in, color @duration ease-in;);
    // }
    &.btn__action:hover {
      color: @white !important;
      background-color: @grey2Dark !important;
    }
  }

  &-text.is-disabled {
    background-color: @disabled;
    color: @disabled-inverted;
  }

  &-text.is-locked {
    background-color: @disabled;
    color: @disabled-inverted;
    cursor: not-allowed;
  }

  &__marking {
    padding: @item-padding * 0.75;
    border-radius: 50%;
  }

  .can-show-marking &__action.is-full-width,
  .can-show-marking &__feedback.is-full-width {
    margin-right: @icon-size + (@item-margin * 2) + ((@item-padding * 0.75) * 2);

    .dir-rtl & {
      margin-right: inherit;
      margin-left: @icon-size + (@item-margin * 2) +
        ((@item-padding * 0.75) * 2);
    }
  }
}
